import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const store = new Vuex.Store({
	state: {
		userInfo: {},
		hasLogin: false,
		token: '',
		searchHistroy: []
	},
	mutations: {
		// 
		login(state, token) {
			state.hasLogin = true
			state.token = token
			console.log("save token", token)
			uni.setStorage({
				key: 'token',
				data: token
			})
		},
		// 
		logout(state) {
			state.hasLogin = false
			this.commit("clearUserInfo")
			uni.removeStorage({
				key: 'token'
			})
		},
		// 
		saveUserInfo (state, userInfo) {
			state.userInfo = userInfo
			console.log("save userInfo", userInfo)
			uni.setStorage({
				key: 'userInfo',
				data: userInfo
			})
		},
		// 
		clearUserInfo(state) {
			state.userInfo = {}
			uni.removeStorage({
				key: 'userInfo'
			})
		},
		// 
		deleteASearchHistroy(state, index) {
			state.searchHistroy.splice(index, 1);
		},
		// 
		getSearchHistroy(state) {
			uni.getStorage({
				key: "searchHistroy",
				success: (res) => {
					state.searchHistroy = res.data;
				},
				fail: (err) => {
					state.searchHistroy = [];
					console.log(err);
				}
			})
		},
		// 
		clearSearchHistroy(state) {
			state.searchHistroy = [];
			uni.removeStorage({
				key: 'searchHistroy'
			})
		},
		// 
		saveSearchHistroy(state, searchHistroy) {
			state.searchHistroy = searchHistroy;
			uni.setStorage({
				key: "searchHistroy",
				data: searchHistroy
			});
		}
	}
})

export default store
